import {all, call, fork, put, takeEvery} from 'redux-saga/effects'
import {toast} from 'react-toastify'
import Cookies from 'js-cookie'

import {SignInTypes} from './types'
import {fetchRequest, authUrls} from '../../api'
import * as authAction from './actions'
import {SignInRequestActionT, SignUpRequestActionT} from '../../interfaces/Store'

/**
 * Sagas
 */
function* signInFetch({payload}: SignInRequestActionT) {
  try {
    yield put(authAction.fetchSignInStart())
    const apiCall = yield call(fetchRequest, authUrls.signIn, {
      body: payload
    })

    if (!apiCall.ok) {
      const apiData = yield apiCall.json()

      toast.warn(apiData.message)
      console.log(apiData.cookie)
      yield put(authAction.signInFetchFailure(`${apiData.message}`))
    } else {
      console.log(apiCall.headers.get('Authorization'))
      console.log(Cookies.get('Authorization'))
      const apiData = yield apiCall.json()

      Cookies.set('authCookie', apiData.cookie)
      yield put(authAction.signInFetchSuccess(apiData.data))
    }
  } catch (err) {
    toast.error('üî•LOGIN ERROR * signInFetch * üî•')
    yield put(authAction.signInFetchFailure('LOGIN ERROR'))
  }
}
function* signUpFetch({payload}: SignUpRequestActionT) {
  try {
    console.log('up')
    yield put(authAction.fetchSignInStart())

    const res = {
      status: 'Success',
      data: {fullName: 'George', email: 'email'}
    }
    console.log(payload)

    yield put(authAction.signInFetchSuccess(res.data))

    // if (res.error) {
    //   yield put(fetchError(res.error))
    // } else {
    //   yield put(fetchSuccess(res))
    // }
  } catch (err) {
    console.log(err)
    yield put(authAction.signInFetchFailure(err))
  }
}
function* googleSignFetch() {
  try {
    console.log('googleSign')
    yield put(authAction.fetchSignInStart())

    const res = {
      status: 'Success',
      data: {fullName: 'George', email: 'email'}
    }

    yield put(authAction.signInFetchSuccess(res.data))

    // if (res.error) {
    //   yield put(fetchError(res.error))
    // } else {
    //   yield put(fetchSuccess(res))
    // }
  } catch (err) {
    console.log(err)
    yield put(authAction.signInFetchFailure(err))
  }
}

/**
 * Watchers
 */
function* watchSignInFetch() {
  yield takeEvery(SignInTypes.signInSaga, signInFetch)
}
function* watchSignUpFetch() {
  yield takeEvery(SignInTypes.signUpSaga, signUpFetch)
}
function* watchGoogleSign() {
  yield takeEvery(SignInTypes.googleSignSaga, googleSignFetch)
}

export function* authSaga() {
  yield all([fork(watchSignInFetch), fork(watchSignUpFetch), fork(watchGoogleSign)])
}
