import React, {FC, useState} from 'react'
import Paper from '@material-ui/core/Paper'

import {AppBar, Drawer} from '../../organisms'
import {useAppLayoutS as useStyle} from './themes'

export const AppLayout: FC = ({children}) => {
  const classes = useStyle()

  const [isShowDrawer, setShowDrawer] = useState(false)

  const showDrawer = () => {
    setShowDrawer(true)
  }
  const hideDrawer = () => {
    setShowDrawer(false)
  }

  /** render **/
  return (
    <Paper className={classes.paper}>
      <AppBar isShow={isShowDrawer} showDrawer={showDrawer} hideDrawer={hideDrawer} />
      <Drawer isShow={isShowDrawer} hideDrawer={hideDrawer} />
      <div className="content" children={children} />
    </Paper>
  )
}
