import React, {FC} from 'react'
import {Switch, Route, Redirect} from 'react-router-dom'
import {useSelector} from 'react-redux'

import routes from './routes'
import {Login} from '../pages'
import {PrivateRoute} from './PrivateRoute'
import {AppLayout} from '../components/layouts'
import {IStore} from '../interfaces/Store'

export const RootRouter: FC = () => {
  const $isAuth = useSelector((state: IStore) => state.$authReducer.isAuth)

  const {publicRoutes, privateRoutes} = routes
  const redirectPath = {pathname: publicRoutes[0].path as string}

  /** render **/
  return (
    <AppLayout>
      <Switch>
        <PrivateRoute redirectPath="/" path="/login" isAuth={!$isAuth} component={Login} />
        {publicRoutes.map((route) => (
          <Route {...route} key={route.path as string} />
        ))}
        {privateRoutes.map((route) => (
          <PrivateRoute {...route} isAuth={$isAuth} key={route.path as string} />
        ))}
        <Redirect from="*" to={redirectPath} />
      </Switch>
    </AppLayout>
  )
}
