import {SignInTypes} from './types'
import {IErrors, ISignInPayload, ISignUpPayload, IProfile} from '../../interfaces/Store'

/**
 * Simple Actions
 */
export const fetchSignInStart = () => ({type: SignInTypes.start})
export const signInFetchSuccess = (payload: IProfile) => ({
  type: SignInTypes.success,
  payload
})
export const signInFetchFailure = (payload: IErrors) => ({type: SignInTypes.failure, payload})

/**
 * Sagas Actions
 */
export const googleSignSaga = () => ({type: SignInTypes.googleSignSaga})
export const signInFetchSaga = (payload: ISignInPayload) => ({
  type: SignInTypes.signInSaga,
  payload
})
export const signUpFetchSaga = (payload: ISignUpPayload) => ({
  type: SignInTypes.signUpSaga,
  payload
})
